const ro = {
  'Quotes and Adages':'Perle și Zicale',
  'Filters' : 'Filtre',
  'Year': 'An',
  'Edition': 'Ediție',
  'Category': 'Categorie',
  'Interlocutors': 'Interlocutori',
"Sorry that some names which should be the same aren't and some which shouldn't be are. Data quality is difficult.": 'Unele nume care ar trebui să fie același nu sunt, iar altele, care n-ar trebui să fie, sunt. Pardon! Calitatea datelor e treabă grea.',
  'Found nothing.': 'N-am găsit nimic.',
  'Perhaps the filters are set in an impossible combination, or you may need to': 'Poate filtrele sunt într-o combinație imposibilă, sau trebuie',
  'login':'să vă logați',
  'logout and login as another user':'să vă delogați și să vă logați ca alt utilizator',
  'from the left bar':'din bara din stânga',
  'Shuffle': 'Aleator',
  'of': 'din',
  'shown': 'vizibile',
  'Show more...': 'Mai multe...'
}

export default function i18n (text, isRo) {
  if (!ro[text])
    console.warn("i18n: translation not found for " + text)

  return isRo && ro[text] || text;
}
